<div class="tutorial"><div class="ui container" id="tutorial-container"><h2>新手任務</h2><progress class="ui progress" :value="(someTaskCompleted || []).filter((b) =&gt; { return b}).length * 25" max="100"></progress><div class="ui list"><div class="item left aligned" v-for="(task, index) in tasks" :key="index"><router-link :to="task.route">{{index+1}}： {{ task.name }}</router-link><span v-show="someTaskCompleted[index]">✔️</span></div></div></div><button class="ui green button" v-if="allTasksCompleted" @click="hideTutorial"><i class="hide icon"></i>隱藏新手任務</button></div>